.code-block {
	display: block;
	width: 100%;
	margin: 0;
	padding: 0;
}

.code-block > label {
	display: block;
	margin-bottom: 0.1rem;
	font-size: 0.75rem;
	font-weight: 500;
	line-height: 1.5;
	color: var(--sc_txt_body__small);
}

.code-block > textarea {
	appearance: none;
	display: block;
	width: 100%;
	height: auto;
	min-height: 72px;
	overflow-y: auto;
	overflow-x: hidden;
	max-height: calc(75dvh - 64px);
	margin: 0;
	padding: 8px;
	font-size: 1rem;
	font-weight: 400;
	line-height: 1.25;
	color: var(--sc_txt_body);
	background-color: var(--sc_bg_body__primary);
	border-top-right-radius: 4px;
	border-top-left-radius: 4px;
	border: var(--sc_border_field);
	border-bottom-style: none;
	transition: border-color .25s, box-shadow .25s;
	resize: vertical;
	text-indent: 0;
	overscroll-behavior: auto;
}
